/*
*
* ==========================================
* GENERAL
* ==========================================
*
*/

.text-small {
    font-size: 0.85rem !important;
}

.text-gray {
    color: #aaa !important;
}

strong {
    font-weight: $font-weight-bold;
}

button {
    &:focus, &:active {
        outline: none;
    }
}


/*
*
* ==========================================
* NAVBAR
* ==========================================
*
*/
.navbar {
    position: relative;
    .dropdown-menu {
        border-top: 2px solid $brand-primary;

        &::before {
            content: '';
            display: block;
            border-left: 0.5rem solid transparent;
            border-right: 0.5rem solid transparent;
            border-bottom: 0.5rem solid $brand-primary;
            position: absolute;
            top: -0.5rem;
            left: 1rem;
        }
    }
}
.navbar-brand {
    @include media-breakpoint-up(lg) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
.navbar {
    .dropdown-menu {
        @include media-breakpoint-up(lg) {
            display: block;
        }
        visibility: hidden;
        opacity:0;
        transform: translateY(0.5rem);
        transition:.2s ease all;
        &.always-animated {
             @include media-breakpoint-down(md) {
                 display: block;
             }
        }
    }
    .dropdown-menu.show {
        display: block;
        visibility: visible;
        opacity:1;
        transform: translateY(0px);
        transition:.2s ease all;
    }
}
.megamenu {
    position: static;

    .dropdown-menu {
        width: 100%;
        z-index: 999;
    }
}



/*
*
* ==========================================
* HERO
* ==========================================
*
*/
.hero {
    min-height: 30rem;
}



/*
*
* ==========================================
* CATEGORY
* ==========================================
*
*/
.category-item {
    display: block;
    position: relative;
    color: $dark;
    transition: all 0.3s;

    img {
        transition: all 0.3s;
    }

    &-title {
        display: inline-block;
        padding: 0.5rem 1rem;
        background: $white;
        text-transform: uppercase;
        letter-spacing: 0.07em;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.07);
        font-size: 0.8rem;
    }

    &:hover {
        img {
            opacity: 0.7;
        }
    }
}




/*
*
* ==========================================
* PRODUCT
* ==========================================
*
*/
.product {
    img {
        filter: grayscale(40%);
        transition: all 0.3s;
    }
    &-overlay {
        width: 100%;
        position: absolute;
        left: 0;
        bottom: 0;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        padding: 1rem 0;
        opacity: 0;
        transition: all 0.3s;

        ul li {
            transition: all 0.3s;
            &:first-of-type {
                transform: translateX(-10px);
                opacity: 0;
            }
            &:last-of-type {
                transform: translateX(10px);
                opacity: 0;
            }
        }
    }

    &:hover {
        img {
            opacity: 0.3;
        }
        .product-overlay {
            opacity: 1;

            li {
                opacity: 1;
                transform: none;
            }
        }
    }

    .badge {
        position: absolute;
        top: 1rem;
        right: 1rem;
        z-index: 999;
    }
}



/*
*
* ==========================================
* PRODUCT MODAL
* ==========================================
*
*/
.product-view {
    min-height: 20rem;
}

.quantity {
    display: flex;
    align-items: center;
    font-family: $headings-font-family;
    input {
        width: 2rem;
        text-align: center;
    }
    button {
        background: none;
        border: none;
        width: 1rem;
        outline: none;
    }
}



/*
*
* ==========================================
* FOOTER
* ==========================================
*
*/
.footer-link {
    font-size: 0.85rem;
    position: relative;
    color: #999;
    transition: all 0.3s;

    &:hover, &:focus {
        padding-left: 0.6rem;
        color: #fff;
        text-decoration: none;

        &::before {
            opacity: 1;
        }
    }

    &::before {
        content: '\f0da';
        display: inline-block;
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 900;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        color: #fff;
        transition: all 0.3s;
        opacity: 0;
    }
}
