/*
*
* ==========================================
* OWL THUMBS
* ==========================================
*
*/
.owl-thumb-item {
    cursor: pointer;
    background: $black;

    img {
        transition: all 0.3s;
    }

    &.active img {
        opacity: 0.8;
    }
}



/*
*
* ==========================================
* NOUISLIDER
* ==========================================
*
*/
.noUi-target {
    background: #efefef;
    border-radius: 0;
    border: none;
    box-shadow: none;
}

.noUi-horizontal {
    height: 2px;

    @include media-breakpoint-down(md) {
        height: 3px;
    }
}

.noUi-horizontal .noUi-handle {
    width: 10px;
    height: 10px;
    right: -10px;
    top: -4px;
    outline: none;
    border-radius: 50%;
    background: $black;
    border: none !important;
    box-shadow: none;
    cursor: pointer;

    @include media-breakpoint-down(md) {
        top: -3px;
    }


    &::before, &::after {
        display: none;
    }
}

.noUi-connect {
    background: $black;
}

.noUi-tooltip {
    font-size: 0.75rem;
    font-family: $headings-font-family;
    background: #fff;
    border-radius: 0;
    border: none;
    color: $black;
    padding: 0.2rem 0;
}

.noUi-handle-lower {
    .noUi-tooltip {
        left: 0;
        transform: none;
    }
}

.noUi-handle-upper {
    .noUi-tooltip {
        left: auto;
        right: 0;
        transform: none;
    }
}



/*
*
* ==========================================
* BOOTSTRAP SELECT
* ==========================================
*
*/
.bootstrap-select {
    display: block !important;
    .bs-select-form-control.dropdown-toggle {
        display: block !important;
        border: 1px solid $input-border-color !important;
        padding: 0.2rem $btn-padding-x-lg !important;
        outline: none !important;
        box-shadow: none;

        &::after {
            margin-left: -0.5rem !important;
        }
    }

    .dropdown-menu {
        padding: 0;
        border-radius: 0;
        border-color: #ddd;
        box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);
        max-height: 200px !important;
        transform: none !important;
        margin: 0 !important;
        max-width: 100%;

        .dropdown-item {
            padding: 0.7rem 1rem;
            transition: all 0.3s;
            outline: none !important;
            font-size: 0.85rem;
            transform: none !important;
            font-weight: 300;
            border-bottom: 1px solid #eee;

            &.active, &:active {
                background: $light !important;
                color: $dark !important;
            }
        }
    }

    .filter-option-inner-inner {
        color: $input-placeholder-color;
        font-size: 0.85rem;
        font-weight: 300;
        font-family: $font-family-sans-serif;
        line-height: 1.5rem !important;
        text-transform: capitalize;
        transform: translateY(1px);
        letter-spacing: 0;

        &.selected {
            color: $black;
            font-size: 0.95rem;
        }
    }

    .bs-searchbox {
        padding: 0.5rem;
        input {
            padding-left: 1rem;
            padding-right: 1rem;
            &:focus {
                box-shadow: none;
                border: 1px solid #ddd;
            }

            &::placeholder {
                font-weight: 300;
            }
        }
    }

    .no-results {
        margin: 0 0.5rem 0.5rem 0.5rem;
        padding: 0.2rem 1rem;
    }


    button.dropdown-toggle {
        &:active, &:focus {
            outline: none !important;
        }
        &::after {
            border: none;
            content: '\f078' !important;
            font-family: 'Font Awesome\ 5 Free';
            font-weight: 900;
            font-size: 0.65rem;
            vertical-align: middle !important;
            padding-left: 0.1rem;
            transform: translateY(-50%);
            position: absolute;
            right: 1rem;
            top: calc(50% + 2px);
        }

        &.form-control-lg {
            .filter-option {
                position: absolute;
                top: 50%;
                left: 1rem;
                transform: translateY(-50%);
                height: 1.5rem;
                line-height: 1.5rem;
            }
        }
    }
    .dropdown-toggle[aria-expanded="true"] {
        &::after {
            content: '\f077' !important;
        }
    }
}
